<template>

	<view class="content">
		<view class="header">
			<view class="image1">
				<image src="/static/images/login/loginlogo.png"></image>
			</view>
			<view class="image2">
				<image src="/static/images/login/logotxt.jpg"></image>
			</view>
		</view>

		<view class="list">
			<view class="list-title">
				<view>用户登录</view>
			</view>
			<view class="input">
				<view class="list-call" style="margin-top: 40rpx;">
					<u-icon name="account" color="#43C6AC" size="40"></u-icon>
					<u--input placeholder="请输入账号" v-model="login.Account" type="number" border="surround"
						clearable></u--input>
					<!-- <input class="sl-input" v-model="login.Account"  maxlength="11" placeholder="输入账号" /> -->
				</view>
				<view class="list-call">
					<u-icon name="lock" color="#43C6AC" size="40"></u-icon>
					<u--input placeholder="请输入密码" v-model="login.Password" border="surround"
						:password="true" clearable></u--input>
					<!-- <input class="sl-input" v-model="login.Password" type="text" maxlength="32" placeholder="输入密码"
					Password="true" /> -->
				</view>
			</view>
			<view class="button-login">
				<u-button open-type="getUserInfo" @getuserinfo="bindGetUserInfo" text="登录" size="large" type="success"
					color="#43C6AC" shape="circle"></u-button>
				<!-- <u-button open-type="getUserInfo" bindgetuserinfo="bindGetUserInfo" text="登录" size="large" type="success"
					color="#43C6AC" shape="circle"></u-button> -->
			</view>
		</view>
	</view>
</template>

<script>
import { postLogin } from "@/nxTemp/apis/login.js"
import { GetUserID } from "@/nxTemp/apis/sunset.js"
export default {
	data() {
		return {
			login: {
				Account: '', //14322
				Password: '', //qqqqqq
				Openid: '',
			}
		};
	},
	onLoad: function () {
		// 获取存储的账号和密码
		const savedAccount = uni.getStorageSync('savedAccount');
		const savedPassword = uni.getStorageSync('savedPassword');
		if (savedAccount && savedPassword) {
			this.login.Account = savedAccount;
			this.login.Password = savedPassword;
		};

		//授权
		wx.getSetting({
			success(res) {
				// if (res.authSetting['scope.userInfo']) {
				// 	// 已经授权，可以直接调用 getUserInfo 获取头像昵称
				// 	wx.getUserInfo({
				// 		success: function (res) {
				// 			uni.showToast({ title: JSON.stringify(res.userInfo), icon: "none" });
				// 		}
				// 	})
				// }
			}
		})
	},
	methods: {
		async bindGetUserInfo(e) {
			if (this.login.Account.length == 0) {
				uni.showToast({
					icon: 'none',
					title: '账号不能为空'
				});
				return;
			}
			if (this.login.Password.length == 0) {
				uni.showToast({
					icon: 'none',  
					title: '密码不能为空'
				});
				return;   
			}

			try {
				if (e.detail.userInfo) {
					this.wxLogin();
				} else {
					uni.showToast({ title: "网络异常!", icon: "none" });
				}
			} catch (erro) {
				uni.showToast({ title: "未知错误!", icon: "none" });
			}
		},

		//调用微信登录接口
		wxLogin() {
			wx.login({
				success: async (res) => {
					if (res.code) {
						const code = res.code;

						//获取用户id
						await GetUserID({
							encryptedData: '',
							iv: '',
							session_key: "",
							loginCode: code
						}).then(res => {
							if (res.success) {
								this.login.Openid = res.subdata;
								this.bindLogin()
							} else {
								uni.showToast({ title: "获取用户id失败!", icon: "none" });
							}
						}).catch(error => {
							uni.showToast({ title: '获取id异常:' + error.errMsg, icon: "none" });
						});
					} else {
						uni.showToast({ title: "获取微信code失败!", icon: "none" });
					}
				},
				fail: () => {
					uni.showToast({ title: "wx.login调用异常!", icon: "none" });
				}
			})
		},

		//登录方法
		async bindLogin() {
			await postLogin(this.login).then(res => {
				if (res.success) {
					uni.setStorage({
						key: 'userData',
						data: res.subdata,
						success: () => {
							//记住账号密码
							uni.setStorageSync('savedAccount', this.login.Account);
							uni.setStorageSync('savedPassword', this.login.Password);
							uni.showToast({ title: '登录成功！' });
							setTimeout(() => {
								// 跳转到首页
								this.$Router.pushTab({ path: '/pages/index/index' });
							}, 1000);
						}
					});
				} else {
					uni.showToast({ title: res.message, icon: "none" });
				}
			}).catch(error => {
				uni.showToast({ title: error, icon: "none" });
				console.error('登录异常:', JSON.stringify(error));
			});
		}
	}
}
</script>

<style>
page {
	background-color: #fdf7f7;
}

.content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin-top: 30rpx;
}

.header {
	width: 100%;
	/* 或者你可以根据需要指定宽度 */
	height: 161rpx;
	margin-top: 200rpx;
	display: flex;
	/* 启用flex布局 */
	justify-content: center;
	/* 水平居中对齐 */
	align-items: center;
	/* 垂直居中对齐 */
	/* background-color: aqua; */
}

.image1 image {
	width: 191rpx;
	height: 191rpx;
}

.image2 image {
	width: 390rpx;
	height: 170rpx;
}


.list {
	display: flex;
	flex-direction: column;
	margin: 40rpx 90rpx 0rpx 90rpx;
	background-color: #FFFFFF;
	border-radius: 19px 19px 19px 19px;
	box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
	padding: 10rpx 10rpx;
}

.list-title {
	height: 100rpx;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 19px 19px 0px 0px;
	background-color: #43C6AC;
	text-align: center;
	font-weight: 900;
	font-size: 30px;
	color: #FFFFFF;
}

.list-call {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	color: #333333;
	border-bottom: 0.5px solid #e2e2e2;
	height: 100rpx;
}

.list-call .sl-input {
	flex: 1;
	text-align: left;
	font-size: 32rpx;
	margin-left: 16rpx;
}

.button-login {
	font-weight: 400;
	font-size: 50rpx;
	color: #FFFFFF;
	width: 380rpx;
	height: 90rpx;
	/* background: linear-gradient(-90deg, #43C6AC, #87CEEB); */
	background-color: #43C6AC;
	border-radius: 50rpx;
	line-height: 100rpx;
	text-align: center;
	margin-left: auto;
	margin-right: auto;
	margin-top: 20rpx;
	margin-bottom: 20rpx;
	box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
}

.button-hover {
	/* background: linear-gradient(-90deg, rgba(193, 25, 32, 0.8), rgba(238, 38, 38, 0.8)); */
	background: #11998e;
}

.agreenment {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	font-size: 30rpx;
	margin-top: 80rpx;
	color: #FFA800;
	text-align: center;
	height: 40rpx;
	line-height: 40rpx;
}

.agreenment text {
	font-size: 24rpx;
	margin-left: 15rpx;
	margin-right: 15rpx;
}

.input {
	padding: 30rpx 70rpx;

}
</style>